import { Component, OnInit } from '@angular/core';
import { Auth } from 'src/app/shared/auth';
import { Crud } from 'src/app/shared/helpers/crud';
import { GlobalService } from 'src/app/shared/services/global.service';
import { Autoloader } from '../../helpers/autoloader';

@Component({
  selector: 'app-career',
  templateUrl: './career.component.html',
  styleUrls: ['./career.component.scss']
})
export class CareerComponent extends Crud implements OnInit {

  breadcrumbList: any = [];
  title: any = "careers";
  cities: any = [];
  areas: any = [];
  resource: any = {};
  $: any = $;
  isUpdate: any = false;
  action: any;
  self: any = this;

  constructor(public service: GlobalService) {
    super(service);


    this.canAdd = true;//Auth.can('career_add');
    this.canEdit = true;//Auth.can('career_edit');
    this.canRemove = true;//Auth.can('career_remove') && Auth.isAdmin();
    //
    this.baseApiUrl = "careers";
    this.initFillable();
  }

  initFillable() {
    this.fillable = [
      {name: 'id'},
      {name: 'name'},
      {name: 'phone'},
      {name: 'email'},
      {name: 'address'},
      {name: 'qualification'},
      {name: 'collage'},
      {name: 'degree'},
      {name: 'industry'},
      {name: 'skills'},
      {name: 'experiance'},
      {name: 'cv_url', type: 'image'},
      {name: 'remove', permission: this.canRemove, action: (item, index) => { this.destroy(item, index)}, class: 'fa fa-trash w3-text-red'},
    ];
  }

  loadSettings() {

    if (Autoloader.areas())
      this.areas = Autoloader.areas();
    else
      this.service.get("areas").subscribe((res) =>{
        this.areas = res;
      });

    if (Autoloader.cities())
      this.cities = Autoloader.cities();
    else
      this.service.get("cities").subscribe((res) =>{
        this.cities = res;
      });
  }


  ngOnInit() {
    this.loadSettings();
    this.initBreadCrumb();
      this.get();
  }

  getAction(res) {
  }

  create() {
    this.isUpdate = false;
    this.resource = {active: 1};
    this.$('#careerModal').modal('show');
  }

  edit(item) {
    this.isUpdate = true;
    this.resource = item;
    this.resource.logo = null;
    this.$('#careerModal').modal('show');
  }

  show(item) {
    this.edit(item);
  }


}
